import React from 'react';
import { Button } from '@mui/material';

const MyButton = ({text, bck, color,link,target}) => {
    return (
        <Button
            href={link}
            target={target}
            variant="contained"
            size="large"
            style={{
                background: bck,
                color: color,
                margin: '20px',
                padding: '10px',

            }}
        >
        {text}
        </Button>
    );
};

export default MyButton;