import React, { useState, useEffect } from 'react';
import MyButton from './MyButton.jsx';

function Popup({ style, score, total, startQuiz, count, MapName }) {
  const [popupState, setPopupState] = useState({
    time: 'start',
    title: 'Welcome to Geospatial Smart Thinkers Quiz',
    text: `This is ${MapName.Name} hazard quiz. <br /> There will be 15 questions. Only one of the options is correct.`,
    buttonText: 'Start the quiz',
  });

  const popupHandle = () => {
    const { time } = popupState;
    if (time === 'start') {
      startQuiz();
    } else {
      window.location.reload(false)
    }
  };

  useEffect(() => {
    if (count === total) {
      setPopupState({
        time: 'end',
        title: 'Congratulations!',
        buttonText: 'Restart',
        text: `You have completed the quiz. <br /> You got: <strong>${score}</strong> out of <strong>${total}</strong> questions right.`,
      });
    }
  }, [count, total, score]);

  const createMarkup = (text) => {
    return { __html: text };
  };

  const { title, text, buttonText } = popupState;

  return (
    <>
      <div className="popup-container" style={style}>
        <div className="ml-5 col-md-10 col-10">
          <div className="popup">
            <h1>{title}</h1>
            <p dangerouslySetInnerHTML={createMarkup(text)} />
            <span onClick={popupHandle}>
              <MyButton text={buttonText} bck="#4d9584" color="#fff" />
            </span>
          </div>
        </div>
      </div>
    </>
  );
}

export default Popup;
