import React, { useState, useEffect } from 'react';
import Answers from './Answers.jsx';
import Popup from './Popup.jsx';
import { useParams } from 'react-router-dom';


const QuizPage = () => {
  const MapName = useParams();
  let QuestionsData;
  if (MapName.Name === 'drought') {
    QuestionsData = require('./QuizQuestionsData.js').DroughtQuestionsData;
  }
  else if (MapName.Name === 'earthquake') {
    QuestionsData = require('./QuizQuestionsData.js').EarthquakeQuestionsData;
  }
  else if (MapName.Name === 'cyclone') {
    QuestionsData = require('./QuizQuestionsData.js').CycloneQuestionsData;
  }
  else if (MapName.Name === 'flood') {
    QuestionsData = require('./QuizQuestionsData.js').FloodQuestionsData;
  }
  else if (MapName.Name === 'lighting') {
    QuestionsData = require('./QuizQuestionsData.js').LightingQuestionsData;
  }
  else if (MapName.Name === 'epidemics') {
    QuestionsData = require('./QuizQuestionsData.js').EpidemicsQuestionsData;
  } else {
    QuestionsData = [];
  }

  const [count, setCount] = useState(0);
  const [total] = useState(QuestionsData.length);
  const [showButton, setShowButton] = useState(false);
  const [questionAnswered, setQuestionAnswered] = useState(false);
  const [score, setScore] = useState(0);
  const [displayPopup, setDisplayPopup] = useState('flex');
  const [question, setQuestion] = useState('');
  const [answers, setAnswers] = useState([]);
  const [correct, setCorrect] = useState('');
  const [ansClassNames, setAnsClassNames] = useState(['', '', '', '']);

  useEffect(() => {
    insertData(count);
    // eslint-disable-next-line
  }, [count]);


  const insertData = (count) => {
    if (count > 0 && count <= total) {
      setQuestion(QuestionsData[count - 1].Question);
      setAnswers(QuestionsData[count - 1].Options);
      setCorrect(QuestionsData[count - 1].Answer);
    }

  }



  const handleShowButton = () => {
    setShowButton(true);
    setQuestionAnswered(true);
  }

  const clearClasses = () => {
    setAnsClassNames(['', '', '', '']);
    setQuestionAnswered(false);
  }

  const nextQuestion = () => {
    if (count === total) {
      setDisplayPopup('flex');
    } else {
      setCount(count + 1)
      insertData(count);
      setShowButton(false);
      setQuestionAnswered(false);
      clearClasses()
    }
  }




  const handleStartQuiz = () => {
    setDisplayPopup('none');
    setCount(1);
  }

  const handleIncreaseScore = () => {
    setScore(score + 1);
  }



  return (
    <div className="main_page_container">
      <div className="container">
        <Popup style={{ display: displayPopup }} MapName={MapName} score={score} total={total} startQuiz={handleStartQuiz} count={count} />

        <div className="question_page_heading">
          <h1>Geospatial Smart Thinkers Quiz</h1>
        </div>


        <div className="row">
          <div className="col-lg-12 col-md-10">
            <div id="question">
              <h4>Question {count}/{total}</h4>
              <p>{question}</p>
            </div>

            <Answers
              answers={answers}
              correct={correct}
              showButton={handleShowButton}
              isAnswered={questionAnswered}
              increaseScore={handleIncreaseScore}
              ansClassNames={ansClassNames}
              setAnsClassNames={setAnsClassNames}
            />

            <div id="submit">
              {showButton ?
                <button className="fancy-btn" onClick={nextQuestion}>
                  {count === total ? 'Finish quiz' : 'Next question'}
                </button> : <span></span>}
            </div>
          </div>
        </div>
      </div>

    </div>

  );
}

export default QuizPage;
